.testimonials {
  margin-top: 152px;

  @include logo;

  &__block {
    @include centered;
  }

  &-article {
    width: 240px;

    &__img {
      margin: 0 0 28px 43px;
    }

    &__h3 {
      color: $c-blue;
      font-size: 24px;
      text-align: center;
    }

    &__h5 {
      font-size: 14px;
      color: $c-grayish;
      font-weight: normal;
      text-align: center;
    }

    &__p {
      text-align: center;
      line-height: 27px;
    }

    &__h3,
    &__h5,
    &__p {
      margin-bottom: 13px;
    }
  }
}
